import { Col, Form, Row } from "antd";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskProjectManageService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { Box, MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import SearchComponent from "../../../core/SearchComponent";
import { ProjectStatusComponent, TagComponent } from "../ProjectStatusComponent";

export default function ModifyComponent() {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [name, setName] = useState('');
    const [content, setContent] = useState('');
    const [status, setStatus] = useState(0);
    const [parentKey, setParentKey] = useState('');
    const [parentName, setParentName] = useState('');
    const [tag, setTag] = useState('');
    const [createdAt, setCreatedAt] = useState(moment());
    const [updatedAt, setUpdatedAt] = useState(moment());
    const [dataParentSearch, setDataParentSearch] = useState([]);

    var keyData = useParams().key;

    const loadData = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            TaskProjectManageService.GetDetail(keyData, (data: any) => {
                const row = data.data.data;
                setName(row.name);
                setContent(row.content);
                setTag(row.tag.toString());
                setStatus(row.status);
                setCreatedAt(moment(row.createdAt));
                setUpdatedAt(moment(row.updatedAt));
                let parentProject = row.parentProject;
                if (parentProject !== null) {
                    setParentKey(parentProject.key);
                    setParentName(parentProject.name);
                }

            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [keyData]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function OnSearch(data: any) {
        setIsLoading(true);
        TaskProjectManageService.Search(data, (data: any) => {
            setDataParentSearch(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    function handleSubmitForm() {
        let body = {
            name: name,
            content: content,
            status: status,
            tag: tag.split(","),
            parentKey: parentKey === '' ? null : parentKey
        };

        setIsLoading(true);
        if (keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        TaskProjectManageService.Add(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            navigate(`/tools/tasks/projects/${data.data.data.key}`)
        }, undefined, () => {
            setIsLoading(false);
        });
    }

    function Edit(body: any) {
        TaskProjectManageService.GetDetail(keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            TaskProjectManageService.Edit(keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                setIsLoading(false);
            }, () => {
                setIsLoading(false);
            });
        }, () => {
            setIsLoading(false);
        });
    }



    function handleDeleteProject() {
        Swal.fire({
            title: 'Bạn có muốn xóa dự án này không?',
            showDenyButton: true,
            confirmButtonText: 'Có',
            denyButtonText: `Không`,
        }).then((result: any) => {
            if (result.isConfirmed) {
                DeleteProject();
            }
        })
    }

    function DeleteProject() {
        setIsLoading(true);
        TaskProjectManageService.GetDetail(keyData, (data: any) => {
            TaskProjectManageService.Delete(keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                setIsLoading(false);
                navigate(`/tools/tasks/projects`);
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Chi tiết dự án"
            isLoading={isLoading}>
            <Box>
                <p>Danh sách các chức năng</p>
                <Row>
                    <Col span={4}>
                        <Link to={`/tools/tasks/projects/${keyData}/sprint`}>
                            <MyButton
                                disabled={keyData === undefined}
                                type="primary"
                                value="Quản lý sprint" />
                        </Link>
                    </Col>
                    <Col span={4}>
                        <Link to={`/tools/tasks/projects/${keyData}/settings`}>
                            <MyButton
                                disabled={keyData === undefined}
                                type="secondary"
                                value="Cài đặt" />
                        </Link>
                    </Col>
                    <Col span={4}>
                        <Link to={`/tools/tasks/projects/${keyData}/dailyReport`}>
                            <MyButton
                                disabled={keyData === undefined}
                                type="dark"
                                value="Daily report" />
                        </Link>
                    </Col>
                    <Col span={4}>
                        <Link to={`/tools/tasks/projects/${keyData}/clone`}>
                            <MyButton
                                disabled={keyData === undefined}
                                type="warning"
                                value="Clone dự án" />
                        </Link>
                    </Col>
                    <Col span={4}>
                        <Link to={`/tools/tasks/projects/${keyData}/codeManage`}>
                            <MyButton
                                disabled={keyData === undefined}
                                type="success"
                                value="Code Manage" />
                        </Link>
                    </Col>
                </Row>
            </Box>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <Row gutter={12}>
                    <Col span={8}>
                        <MyFormItem.Input
                            readOnly={true}
                            label="Mã dự án"
                            value={keyData}
                            setValue={undefined} />
                    </Col>
                    <Col span={16}>
                        <MyFormItem.Input
                            label="Tên dự án"
                            value={name}
                            setValue={setName} />
                    </Col>
                    <Col span={24}>
                        <MyFormItem.TextArea
                            label="Nội dung"
                            value={content}
                            setValue={setContent} />
                    </Col>
                    <Col span={24}>
                        <TagComponent
                            projectKey={keyData}
                            label="Tag"
                            value={tag}
                            setValue={setTag} />
                    </Col>
                    <Col span={12}>
                        <SearchComponent
                            label="Dự án cha"
                            muityChoose={false}
                            onSearch={OnSearch}
                            resultKey={parentKey}
                            resultName={parentName}
                            setResultKey={setParentKey}
                            setResultName={setParentName}
                            dataSearch={dataParentSearch}
                            isLoading={isLoading} />
                    </Col>
                    <Col span={12}>
                        <ProjectStatusComponent
                            label="Trạng thái"
                            projectKey={keyData}
                            type="1"
                            value={status}
                            setValue={setStatus} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.DateTimePicker
                            label="Ngày tạo"
                            readOnly={true}
                            value={createdAt}
                            setValue={setCreatedAt} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.DateTimePicker
                            label="Ngày cập nhật cuối cùng"
                            readOnly={true}
                            value={updatedAt}
                            setValue={setUpdatedAt} />
                    </Col>
                    <Col span={8}>
                        <MyButton
                            htmlType="submit"
                            type="success"
                            value="Thay đổi" />
                    </Col>
                    <Col span={8}>
                        <MyButton
                            onClick={handleDeleteProject}
                            disabled={keyData === undefined}
                            type="danger"
                            value="Xóa" />
                    </Col>
                    <Col span={8}>
                        <Link to="/tools/tasks/projects">
                            <MyButton
                                type="warning"
                                value="Hủy" />
                        </Link>
                    </Col>
                </Row>
            </Form>
        </MyBox>
    );
}